@charset "utf-8";

/* Reset */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
blockquote,
pre,
code,
address,
ul,
ol,
li,
menu,
nav,
section,
article,
aside,
dl,
dt,
dd,
table,
thead,
tbody,
tfoot,
label,
caption,
th,
td,
form,
fieldset,
legend,
hr,
input,
button,
textarea,
object,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

html,
body {
  width: 100%;
}

html {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body,
input,
select,
textarea,
button {
  border: none;
  font-size: 12px;
  font-family: 'Noto Sans KR', sans-serif;
  color: #727272;
}

ul,
ol,
li {
  list-style: none;
}

address,
cite,
code,
em {
  font-style: normal;
  font-weight: normal;
}

main,
header,
section,
nav,
footer,
aside,
article,
figure {
  display: block;
}
a {
  color: #000;
  text-decoration: none;
}
